<template>
  <div>
    <ui-slider class="mt30" showMarker="true" min="0" max="500" v-model="time"></ui-slider>
    <div class="body df aic jcsa">
      <div class="code br5 df ffcn">
        <div v-for="i in viewCode" :key="i" class="df">
          <p v-for="j in i" :value="j" class="df aife">{{j==' '?'&nbsp;':j}}</p>
        </div>
      </div>
      <css-html class="html br5">
        <slot v-if="showHtml" />
      </css-html>
    </div>
  </div>
</template>

<script>
import { UiSlider } from "keen-ui";
import CssHtml from "./html.vue";
export default {
  name: "CssCode",
  components: { CssHtml, UiSlider },
  props: {
    html: {
      type: Array,
      default() {
        return [];
      }
    },
    time: {
      type: Number,
      default() {
        return 100;
      }
    }
  },
  data() {
    return {
      viewCode: [], //显示在页面的代码
      code: "", //用来储存的临时代码片段
      lshtml: [], //用来储存临时的代码文章
      showHtml: false //用来控制文章的显示和隐藏
    };
  },
  watch: {
    html(html) {
      //代码改变时候触发
      this.lshtml = JSON.parse(JSON.stringify(html));
      this.htmlChange();
    }
  },
  mounted() {
    console.log(this.code);
  },
  methods: {
    //当html改变时
    htmlChange() {
      if (this.lshtml.length == 0) {
        this.showHtml = true;
        return;
      }
      this.viewCode.push(""); //执行换行
      this.code = JSON.parse(JSON.stringify(this.lshtml[0]));
      this.codeChange();
      this.lshtml.splice(0, 1); //删除html最新一行
    },
    codeChange() {
      setTimeout(() => {
        this.$set(
          this.viewCode,
          this.viewCode.length - 1,
          this.viewCode[this.viewCode.length - 1] +
            String(this.code.substr(0, 1))
        );
        this.code = this.code.substr(1);
        if (this.code.length != 0) {
          this.codeChange();
        } else {
          this.htmlChange();
        }
      }, this.time);
    }
  }
};
</script>

<style>
.code {
  text-indent: 0.3em;
  /* letter-spacing: 0.3em; */
}
.code * {
  margin: 0;
  padding: 0;
  color: rgb(222, 222, 222);
}
p[value*=":"],
p[value*=";"],
p[value*="'"],
p[value*="."],
p[value*="-"],
p[value*=","],
p[value*="#"],
p[value*="*"],
p[value*='"'] {
  color: rgb(42, 161, 152);
}

p[value*="<"],
p[value*=">"],
p[value*="("],
p[value*=")"],
p[value*="{"],
p[value*="}"] {
  color: yellow;
}

p[value*="a"],
p[value*="b"],
p[value*="c"],
p[value*="d"],
p[value*="e"],
p[value*="f"],
p[value*="g"],
p[value*="h"],
p[value*="i"],
p[value*="j"],
p[value*="k"],
p[value*="l"],
p[value*="m"],
p[value*="n"],
p[value*="o"],
p[value*="p"],
p[value*="q"],
p[value*="r"],
p[value*="s"],
p[value*="t"],
p[value*="u"],
p[value*="v"],
p[value*="w"],
p[value*="x"],
p[value*="y"],
p[value*="z"],
p[value*="A"],
p[value*="B"],
p[value*="C"],
p[value*="D"],
p[value*="E"],
p[value*="F"],
p[value*="G"],
p[value*="H"],
p[value*="I"],
p[value*="J"],
p[value*="K"],
p[value*="L"],
p[value*="M"],
p[value*="N"],
p[value*="O"],
p[value*="P"],
p[value*="Q"],
p[value*="R"],
p[value*="S"],
p[value*="T"],
p[value*="U"],
p[value*="V"],
p[value*="W"],
p[value*="X"],
p[value*="Y"],
p[value*="Z"] {
  color: rgb(133, 153, 0);
  text-indent: 0.15em;
}
p[value*="0"],
p[value*="1"],
p[value*="2"],
p[value*="3"],
p[value*="4"],
p[value*="5"],
p[value*="6"],
p[value*="7"],
p[value*="8"],
p[value*="9"] {
  color: rgb(187, 137, 0);
  text-indent: 0.15em;
}

.body > div {
  margin-top: 10px;
  width: 45vw;
  height: 90vh;
  border: 1px #fff solid;
  overflow: auto;
}
</style>


