<template>
  <div>
    <css-code :html="html">
      <div>
        <div class="div">你好。这是一个 div 元素。</div>
        <div class="div" id="div2">你好。这是一个 div 元素。</div>
      </div>
    </css-code>
  </div>
</template>

<script>
import CssCode from "@/components/CSS/code.vue";
export default {
  name: "CssBasicCourses",
  components: {
    CssCode
  },
  data() {
    return {
      html: []
    };
  },
  mounted() {
    this.html = [
      "/*大家好!!!",
      "*这是老虎从w3c上面总结的css案例，我将详细的一一动态解析，有酷炫的动态效果哦。-->  uaioe@qq.com",
      " ",
      " ",
      "*CSS3",
      "*CSS 用于控制网页的样式和布局。",
      "*CSS3 是最新的 CSS 标准。",
      "*本教程向您讲解 CSS3 中的新特性。",
      "*CSS3 实例",
      "*/",
      " ",
      "<!DOCTYPE html>",
      "<html>",
      "<head>",
      "<style> ",
      "div {",
      "  width: 100px;",
      "  height: 75px;",
      "  background-color: yellow;",
      "  border: 1px solid black;",
      "}",
      "div#div2 {",
      "  transform: rotate(30deg);",
      "  -ms-transform: rotate(30deg); /* IE 9 */",
      "  -moz-transform: rotate(30deg); /* Firefox */",
      "  -webkit-transform: rotate(30deg); /* Safari and Chrome */",
      "  -o-transform: rotate(30deg); /* Opera */",
      "}",
      "</style>",
      "</head>",
      "<body>",
      " ",
      "<div>你好。这是一个 div 元素。</div>",
      " ",
      "<div id='div2'>你好。这是一个 div 元素。</div>",
      " ",
      "</body>",
      "</html>"
    ];
  },
  methods: {
    aa() {
      var bb = "sfd\n";
    }
  }
};
</script>

<style scoped>
.html {
  height: 100vh;
  width: 100vw;
}
.div {
  color:black;
  width: 100px;
  height: 75px;
  background-color: yellow;
  border: 1px solid black;
}
.div#div2 {
  transform: rotate(30deg);
  -ms-transform: rotate(30deg); /* IE 9 */
  -moz-transform: rotate(30deg); /* Firefox */
  -webkit-transform: rotate(30deg); /* Safari and Chrome */
  -o-transform: rotate(30deg); /* Opera */
}
</style>



